import React, { useEffect, useState } from 'react';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import axios from 'axios';
import LazyLoad from 'react-lazy-load';
import logo from '../../../../assets/ico/euroQualLogo.webp';
import Helmet from 'react-helmet';

const Results = () => {
    const [results, setResults] = useState([]);
    const [tourSeparators, setTourSeparators] = useState([]);

  function convertGermanToclientTime(germanTime) {
    //       
    const [hours, minutes] = germanTime.split(':').map(Number);
  
    //   Date         
    const germanDate = new Date();
    germanDate.setHours(hours);
    germanDate.setMinutes(minutes);

    const clientUTCOffset = new Date();
  
    //        (2.5 )
    const clientDate = new Date(germanDate.getTime() + ((-clientUTCOffset.getTimezoneOffset() / 60) - 2) * 60 * 60 * 1000);
  
    //      ":"
    const clientTime = `${clientDate.getHours()}:${clientDate.getMinutes().toString().padStart(2, '0')}`;
  
    return clientTime;
  }

    useEffect(() => {
      window.scrollTo(0, 0); // scroll top, when open page
  }, []);

  useEffect(() => {
    const fetchData = async () => {
        try {
          const response = await axios.get('/results/euQual');
          const data = response.data;
  
          setResults(data);
  
          //       
          const separators = [];
          let currentRound = '';
  
          for (let i = 0; i < data.length; i++) {
            const round = data[i].round;
  
            if (round !== currentRound) {
              separators.push(i);
              currentRound = round;
            }
          }
  
          setTourSeparators(separators);
        } catch (error) {
          console.log(error);
        }
      };
  
      fetchData();
}, []);

    return (
        <div className='leagueResults'>
          <Helmet>
                <title>  -   -  Legendary Football</title>
                <meta name="description" content="     ." />
                <meta name="keywords" content=" ,  ,  , ,  ,  ,  ,  ,   ,    " />
            </Helmet>
            <div className="logoPageName">
                <LazyLoad offset={800}>
                    <Tippy content=' '><img loading="lazy" src={logo} alt="logo" /></Tippy>
                </LazyLoad>
                <h1 className="pageName"> -  </h1>
            </div>
            {results && results.length > 0 ? results.map((fixture, index) => (
        <React.Fragment key={'euQual' + index}>
          {tourSeparators.includes(index) && <hr />} {/*  ,   */}
          <div className="col">
            
            <div className="round" style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {background: '#ffbf66'} : {background: '#f02d54'}}>
              <span style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {color: '#000'} : {color: '#fff'}}>{fixture.round}</span>
            </div>
            <div className="center">
              <span className="hName">{fixture.hName}</span>
              <LazyLoad offset={800}>
                <Tippy content={fixture.hName}>
                  <img loading="lazy" src={fixture.hLogo} alt={fixture.hName} />
                </Tippy>
              </LazyLoad>
              <span className="hScore" style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {background: '#fff'} : {background: '#f02d54', color: '#fff', borderColor: '#f02d54'}}>
                {fixture.hScore}
              </span>
              -
              <span className="aScore" style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {background: '#fff'} : {background: '#f02d54', color: '#fff', borderColor: '#f02d54'}}>
                {fixture.aScore}
              </span>
              <span></span>
              <LazyLoad offset={800}>
                <Tippy content={fixture.aName}>
                  <img loading="lazy" src={fixture.aLogo} alt={fixture.aName} />
                </Tippy>
              </LazyLoad>
              <span className="aName">{fixture.aName}</span>
            </div>
            <div className="dateTime" style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {background: '#ffbf66'} : {background: '#f02d54'}}>
              <span style={fixture.dateTime === '' || fixture.dateTime.includes(':') ? {color: '#000'} : {color: '#fff'}}>
                {fixture.dateTime.includes(':') ? (fixture.dateTime.includes(',') ? fixture.dateTime.split(',')[0] + ', ' + convertGermanToclientTime(fixture.dateTime.split(',')[1]) : ', ' + convertGermanToclientTime(fixture.dateTime)) : fixture.dateTime}
              </span>
            </div>
          </div>
        </React.Fragment>
      )) : <div className='noData'> </div>}
        </div>
    );
};

export default Results;