import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import './LeagueAll.css';
import LazyLoad from 'react-lazy-load';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import Helmet from 'react-helmet';

const LeagueAll = (props) => {
    const[leagues, setLeagues] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        setLeagues(props.leagues.map((e, i) => {
            return <Link key={'league' + i} to={`/league/${e.id}`}>
                        <div>
                            <LazyLoad offset={800}>
                                <Tippy content={e.title}><img loading="lazy" src={e.img} alt={e.name} /></Tippy>
                            </LazyLoad>
                            <span>{e.name}</span>
                        </div>
                    </Link>
        }));
    }, [props.leagues]);
    

    return (
        <div id='leagueAll'>
            <Helmet>
                <link rel="canonical" href="https://legfootball.com/league" />
                <title>  -       , , ,      -  Legendary Football</title>
                <meta name="description" content="        ,       .          ,  .     ." />
                <meta name="keywords" content=" ,  , ,  , ,   , ,  , , ,  ,  1, " />
            </Helmet>
            <h1 className="pageName"> </h1>
            {leagues}
        </div>
    );
};

export default LeagueAll;